.App {
  text-align: center;
  margin: auto 15px;
}
.header {
  margin: 50px auto;
  color: black;
  background: white;
}
.content{
  display: -ms-flexbox;
  display: flex;
}
.todo-input{
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 30px;
  background: #D5D5D5;
  border: 1px black
}
.todo-btn{
  width: 100px;
  height: 50px;
  color: #efefef;
  background: #06B806; 
  font-size: 20px; 
  border: 1px #363636
}
.todo{
  text-align: left;
  padding: 10px;
  border: 1px #1F1F1F solid;
  -ms-flex:1 1;
      flex:1 1;
  background: #363636;
  color: #D7D7D7;
  border: 1px white; 

}
.remove-btn{
  width: 40px;
  height: 40px;
  color: #feeffe;
  background: #9C0000; 
  font-size: 15px; 
  border: 1px #363636;
}
.todo-wrapper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.input-checkbox{
  width:  80px;
  height : 40px;
}


